import React from 'react'
import "./CSS/Quora.css"
import Feed from './Feed'
import Navbar from './Navbar'
import Sidebar from './Sidebar'
import Widjet from './Widjet'


function Quora() {
    return (
        <div className="quora">
            <Navbar/>
            <div className="quora-content">
            <Sidebar/>
            <Feed/>
            <Widjet />
        </div>
        </div>
    )
}

export default Quora
